import React from 'react';
import Github from '../assets/icons/Github.svg';
import Gmail from '../assets/icons/Gmail.svg';
import Linkedin from '../assets/icons/Linkedin.svg';

const GmailIconGroup = () => {
    const content = {
        display:'flex',
        flexDirection:'column',
        position:'absolute',
        top:'20vh',
        left:'2em',
        height:'30vh',
    }

    const icon = {
        display:'flex',
        flex:1,
        cursor:'pointer',
        marginTop:'10vh',
        marginLeft:'10px',
        padding:'10px',
        
    }
    return (
        <div style={content}>
            <img style={icon} alt='git' id='iconGit' src={Github} height={22}/>
            <img style={icon} alt='gmail' id='iconGit' src={Gmail} height={22}/>
            <img style={icon} alt='linkedin' id='iconGit' src={Linkedin} height={22}/>
        </div>
    )
}

export {GmailIconGroup};