import React, { useState, useEffect } from 'react';
import 'react-awesome-slider/dist/styles.css';
import 'react-awesome-slider/dist/custom-animations/cube-animation.css';
import AwesomeSlider from 'react-awesome-slider';
import Landing from './components/Landing';
import Sidebar from './components/Sidebar';
import { useSelector, useDispatch } from 'react-redux';


const App = () => {
  const total = 2;
  const { index } = useSelector(state => state.dataReducer);
  const dispatch = useDispatch();
  const setIndex = (ind) => {
    dispatch(
      {
        type: 'updateState',
        payload: {
          index: ind,
          menuOpen: false
        }
      }
    )
  }
  const [transition, setTransition] = useState(true);


  const Slider = (

    <AwesomeSlider
      animation="cubeAnimation"
      fillParent={true}
      onFirstMount={() => { setTransition(false) }}
      bullets={false}
      buttons={false}
      selected={index}
      onTransitionStart={() => setTransition(true)}
      onTransitionEnd={(e) => {
        setIndex(e.currentIndex);
        setTimeout(() => {
          setTransition(false);
        }, 400);
      }}>
      <div id='0'><Landing /></div>
      <div id='1'><div style={{
        height: '100vh',
        width: '100vw',
        color: 'white',
        position: 'relative', background: '#1473E6'
      }} /></div>
    </AwesomeSlider>
  );

  useEffect(() => {

    const keyHandler = (event) => {

      if (transition)
        return;
      switch (event.key) {
        case "ArrowLeft":
          event.preventDefault();
          setIndex(index === 0 ? total - 1 : index - 1);
          break;

        case "ArrowRight":
          event.preventDefault();
          setIndex(index === total - 1 ? 0 : index + 1);
          break;

        case "ArrowUp":
          event.preventDefault();
          document.getElementById(Slider.props.selected).scrollTop -= 800;
          break;

        case "ArrowDown":
          // Down pressed 
          event.preventDefault();
          document.getElementById(Slider.props.selected).scrollTop += 800;
          break;

        default:
          break;
      }
    }
    window.addEventListener("keydown", keyHandler);
    return () => {
      window.removeEventListener("keydown", keyHandler);
    };
  });


  return (
    <div><Sidebar />{Slider}</div>
  );
}

export default App;
