import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
const backgroundColors = ['rgb(63 63 63)', '#1473E6'];

const Sidebar = () => {
    const dispatch = useDispatch();
    const { menuOpen, index } = useSelector(state => state.dataReducer);
    useEffect(()=>{
        if(menuOpen)
            document.getElementsByClassName('awssld--active')[0].style['transform'] = 'translate3d(-100px, 0px, -600px) rotateY(20deg)';
        if(!menuOpen)
            document.getElementsByClassName('awssld--active')[0].style['transform'] = 'translate3d(0,0,0)';
    }, [menuOpen])
    const setIndex = (ind) => {
        dispatch(
            {
                type: 'updateState',
                payload: {
                    index: ind,
                    menuOpen: false
                }
            }
        )
    }

    const hamburger = {
        position: 'fixed',
        top: '20px',
        right: '50px',
        cursor: 'pointer',
        zIndex: 20
    }

    const line = {
        width: '28px',
        height: '3px',
        background: 'white',
        marginTop: '5px',
        transition: 'all 0.2s'
    }
    const menu = {
        position: 'fixed',
        width: '350px',
        bottom: 0,
        top: 0,
        background: backgroundColors[index],
        zIndex: 18,
        right: 0,
    }
    console.log({ menuOpen});

    return (
        <div>
            <div className='hamburger' style={hamburger} onClick={() => {
                dispatch({ type: 'updateState', payload: { menuOpen: !menuOpen } });

            }} >
                <div style={!menuOpen ? { ...line } : { ...line, opacity: 0 }} />
                <div style={!menuOpen ? { ...line } : { ...line, transform: ' rotate(45deg)' }} />
                <div style={!menuOpen ? { ...line } : { ...line, transform: 'translateY(-8px) rotate(-45deg)' }} />
            </div>
            <div className='menu' style={menuOpen ? menu : { display: 'none' }} >
                <div className='menu-inner' style={{ height: '100%', width: '100%', backdropFilter: 'blur(8px) brightness(0.8)', display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                    <div style={{ height: '45vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', textAlign: 'center' }}>
                        <div className={index === 0 ? 'menu-items active' : 'menu-items'} onClick={() => { setIndex(0) }}>
                            Home
                        </div>
                        <div className={index === 1 ? 'menu-items active' : 'menu-items'} onClick={() => { setIndex(1) }}>
                            Projects
                        </div>
                        <div className={index === 2 ? 'menu-items active' : 'menu-items'} onClick={() => { setIndex(2) }}>
                            About
                        </div>
                        <div className={index === 3 ? 'menu-items active' : 'menu-items'} onClick={() => { setIndex(3) }}>
                            Contact
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Sidebar;