import React from 'react';
import Background from '../assets/background.png';
import Arrow from '../assets/icons/home-button-arrow.svg';
import {GmailIconGroup} from './Navigate';

const Landing = () => {

  const outer = {
    height:'100vh',
    width:'100vw',
    color:'white',
    position:'relative',
    overflowY:'scroll',
    
  }

  const background = {
    position:'absolute',
    top:0,
    left:0,
    height:'100%',
    pointerEvents:'none',
    width:'100%',
  }
  
  const content = {
    transform: 'translateZ(2px)',
    padding:'30px',
    pointerEvents:'none'
    
  }
  const title = {
    position:'absolute',
    top:'45vh',
    textAlign:'center',
    left:0,
    right:0,
    transform:'translate(0,-50%) translateZ(2px)',
    fontSize:'3em',
    pointerEvents: 'none'

  }

  const end = {
    position:'absolute',
    bottom:'50px',
    right:'50px',
    fontSize:'1.25em',
    color:'#A6A6A6'
  }

  return (
    <div className='outer' style={outer}>
        <img alt='' style={background} src={Background} />
        <GmailIconGroup/>
        <div className='content' style={content}>
          Rishabh Chaudhary
          
            <div className='title' style={title}>
                Bringing Life to Ideas<br/>
                <div id='button'>
                  Projects
                  <img id='icon' alt='' src={Arrow} height={16} style={{padding:'5px',marginLeft:'10px'}} />
                </div>
            </div>
        </div>  
        <div className='end' style={{...content,...end}}>
        use arrow keys to navigate
        </div>
    </div>
    
  );
}

export default Landing;